<template>
  <div>
    <el-table
      :data="applyCANRecordList"
      empty-text="No data"
      style="width:100%">
      <!--日期-->
      <el-table-column
        label="Date"
        >
        <template slot-scope="scope">
          <p class="">{{ scope.row.createdAt | subDate }}</p>
        </template>
      </el-table-column>
      <!--收币地址-->
      <el-table-column
      label="Payee address"
      prop="canReceiveAddress">
      </el-table-column>
      <!--提币数 (个)-->
      <el-table-column
        label="Drawing number"
        prop="amount">
      </el-table-column>
      <!--状态-->
      <el-table-column
        label="State">
        <template slot-scope="scope">
          <p class="theme-fontColor">{{ scope.row.status | applyCanStatus }}</p>
        </template>
      </el-table-column>
    </el-table></div>
</template>

<script>
  import axios from 'axios'
  import { UTIL } from '../../util/util'
  export default {
    data () {
      return {
        applyCANRecordList: []
      }
    },
    mounted () {
      this.applyRecordDataRequest()
    },
    methods: {
      // 提币申请记录查询接口
      applyRecordDataRequest () {
        let vm = this
        axios.get('/promo/authed/account/coins/extract/lists/1/5')
          .then(function (response) {
            console.log(response.data)
            vm.applyCANRecordList = UTIL.fliterNullArr(response.data)
          })
          .catch(function (error) {
            console.log(error)
          })
      }
    }
  }
</script>
<style scoped>

</style>



